import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import {
  Home,
  Antwoorden,
  Navigation,
  Footer,
  FormulierenRouter,
  Klassement,
  Contact,
  Admin,
  Login,
} from "./pages";
import { DeelnamesProvider } from "./contexts/deelnames";
import { DeelnemersProvider } from "./contexts/deelnemers";
import { KlassementProvider } from "./contexts/klassement";
import { TekstbestandenProvider } from "./contexts/tekstbestanden";
import { useMediaQuery } from "react-responsive";
import "./assets/css/scroll.css";
import "./assets/css/colors.scss";
import { AuthProvider } from "./contexts/auth";
import PrivateRoute from "./components/PrivateRoute";

function App() {
  const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isLaptop = useMediaQuery({ query: "(min-width: 1224px)" });

  return (
    <>
      <AuthProvider>
        {isLaptop && (
          <div className="App back">
            <DeelnamesProvider>
              <DeelnemersProvider>
                <KlassementProvider>
                  <TekstbestandenProvider>
                    <Router>
                      <Navigation />
                      <Switch>
                        <Route path="/" exact component={() => <Home />} />
                        <Route
                          path="/antwoorden"
                          exact
                          component={() => <Antwoorden />}
                        />
                        <Route
                          path="/klassement"
                          exact
                          component={() => <Klassement />}
                        />
                        <Route
                          path="/formulieren"
                          exact
                          component={() => <FormulierenRouter />}
                        />
                        <Route
                          path="/contact"
                          exact
                          component={() => <Contact />}
                        />
                        <Route
                          path="/login"
                          exact
                          component={() => <Login />}
                        />
                        <PrivateRoute
                          path="/admin"
                          exact
                          component={() => <Admin />}
                        />
                      </Switch>
                      <Footer />
                    </Router>
                  </TekstbestandenProvider>
                </KlassementProvider>
              </DeelnemersProvider>
            </DeelnamesProvider>
          </div>
        )}
        {isMobile && (
          <div className="App back">
            <DeelnamesProvider>
              <DeelnemersProvider>
                <KlassementProvider>
                  <TekstbestandenProvider>
                    <Router>
                      <Navigation />
                      <Switch>
                        <Route path="/" exact component={() => <Home />} />
                        <Route
                          path="/antwoorden"
                          exact
                          component={() => <Antwoorden />}
                        />
                        <Route
                          path="/klassement"
                          exact
                          component={() => <Klassement />}
                        />
                        <Route
                          path="/formulieren"
                          exact
                          component={() => <FormulierenRouter />}
                        />
                        <Route
                          path="/contact"
                          exact
                          component={() => <Contact />}
                        />
                        <Route
                          path="/admin"
                          exact
                          component={() => <Admin />}
                        />
                      </Switch>
                    </Router>
                  </TekstbestandenProvider>
                </KlassementProvider>
              </DeelnemersProvider>
            </DeelnamesProvider>
          </div>
        )}
      </AuthProvider>
    </>
  );
}

export default App;
