import React, { useState, useContext } from "react";
import PropTypes from "prop-types";
import { TekstbestandenContext } from "../contexts/tekstbestanden";
import "../assets/css/background.scss";

function Home({ setToken }) {
  const [home1, setHome1] = useState();
  const [home2, setHome2] = useState();

  const contextTekstbestanden = useContext(TekstbestandenContext);

  const handleUpdaten = async () => {
    await contextTekstbestanden.updateTekstbestand(home1, home2);
  };

  const handleReturn = () => {
    setToken("buttons");
  };

  return (
    <div className="box tekst">
      <div className="my-5 pt-5">
        <div className="mx-5 px-5">
          <div className="mb-5">
            <p className="my-4">
              <strong>
                <em className="titels">
                  Hier kan u uw welkomstteksten aanpassen
                </em>
              </strong>
            </p>
            <div>
              <form>
                <div className="row">
                  <textarea
                    type="home1"
                    className="form-control col-md-6"
                    id="home1"
                    placeholder="Hier komt uw tekst voor uw verwelkoming"
                    onChange={(h1) => setHome1(h1.target.value)}
                  ></textarea>
                  <textarea
                    type="home2"
                    className="form-control col-md-6"
                    id="home2"
                    placeholder="Hier komt uw tekst voor uw aankondigingen"
                    onChange={(h2) => setHome2(h2.target.value)}
                  ></textarea>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <button
                      className="btn col-md-4 mr-3"
                      onClick={handleUpdaten}
                    >
                      Updaten
                    </button>
                    <button
                      className="btn col-md-4 ml-3"
                      onClick={handleReturn}
                    >
                      Terugkeren
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

Home.propTypes = { setToken: PropTypes.func.isRequired };

export default Home;
