import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from "react";
import * as deelnamesAPI from "../api/deelnames";

export const DeelnamesContext = createContext();
export const useDeelnames = () => useContext(DeelnamesContext);

export const DeelnamesProvider = ({ children }) => {
  const [initialLoad, setInitialLoad] = useState(false);
  const [deelnames, setDeelnames] = useState([]);

  const refreshDeelnames = useCallback(async () => {
    try {
      const { data } = await deelnamesAPI.refreshDeelnames();
      setDeelnames(data.data);
      return data.data;
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => {
    if (!initialLoad) {
      refreshDeelnames();
      setInitialLoad(true);
    }
  }, [initialLoad, refreshDeelnames]);

  const updateDeelname = useCallback(async () => {
    try {
      await deelnamesAPI.updateDeelname();
      refreshDeelnames();
    } catch (error) {
      console.log(error);
    }
  }, [refreshDeelnames]);

  const deleteDeelnemer = useCallback(
    async (deelnemer) => {
      await deelnamesAPI.deleteDeelnemer(deelnemer);
      refreshDeelnames();
    },
    [refreshDeelnames]
  );

  const value = useMemo(
    () => ({
      deelnames,
      updateDeelname,
      deleteDeelnemer,
    }),
    [deelnames, updateDeelname, deleteDeelnemer]
  );

  return (
    <DeelnamesContext.Provider value={value}>
      {children}
    </DeelnamesContext.Provider>
  );
};
