import React, { useState, useContext } from "react";
import { axios } from "../api/index";
import PropTypes from "prop-types";
import { DeelnemersContext } from "../contexts/deelnemers";
import { KlassementContext } from "../contexts/klassement";

function Uitschrijven({ setToken }) {
  const [alias, setAlias] = useState();

  const contextKlassement = useContext(KlassementContext);
  const contextDeelnemers = useContext(DeelnemersContext);

  const handleSubmit2 = async (e) => {
    e.preventDefault();
    const deelnemers = await axios.get("/deelnemers");
    const deelnemer = deelnemers.data.filter(
      (deelnemer) => deelnemer.alias === alias
    );
    if (deelnemer[0]) {
      await contextKlassement.deleteKlassement(deelnemer);
      await contextDeelnemers.deleteDeelnemer(deelnemer);
      setToken("buttons");
    } else setToken("foutverwijderen");
  };

  return (
    <>
      <form
        className="mb-5 pb-5 ml-3 mr-3 fixed-bottom"
        onSubmit={handleSubmit2}
      >
        <br />
        <div className=" tekst form-row">
          <div className="form-group col-md-8 ml-4 mr-5">
            <input
              required
              type="alias"
              className="form-control"
              id="alias"
              placeholder="Alias (middelste kolom)"
              onChange={(ali) => setAlias(ali.target.value)}
            />
          </div>
          <div className="form-group col-md-2 pl-5 ml-5">
            <button type="submit" className="btn">
              Uitschrijven
            </button>
          </div>
        </div>
      </form>
    </>
  );
}
Uitschrijven.propTypes = { setToken: PropTypes.func.isRequired };

export default Uitschrijven;
