import { axios } from ".";

export const refreshDeelnemers = async () => {
  return axios.get("/deelnemers");
};

export const createDeelnemer = async (info) => {
  await axios.post("/deelnemers", info);
};

export const getDeelnemerById = async (id) => {
  return await axios.get(`/deelnemers/${id}`);
};

export const login = async (alias, wachtwoord) => {
  return await axios.post(`/deelnemers/login`, { alias, wachtwoord });
};

export const updateDeelnameDeelnemer = async (deelnemer, deelname) => {
  await axios.put(`/deelname/${deelnemer[0].id}`, { deelname });
  // await axios({
  //   method: "put",
  //   url: `/deelname/${deelnemer[0].id}`,
  //   data: { deelname: deelname },
  // });
};

export const deleteDeelnemer = async (deelnemer) => {
  await axios.delete(`/deelnemers/${deelnemer[0].id}`);
  // await axios({
  //   method: "delete",
  //   url: `http://localhost:9000/api/deelnemers/${deelnemer[0].id}`,
  // });
};
