import React from "react";
import "../assets/css/background.scss";
import "../assets/css/colors.scss";

function AlGebruikt() {
  return (
    <div className="box">
      <p className="titels mt-5 pt-5 mr-3">
        De alias en/of de naam dat u ingaf zijn al in gebruik. Gelieve een
        andere alias aan te maken of, indien dat het geval is, slechts nmaal
        te registreren en niet meermaals.
      </p>
    </div>
  );
}

export default AlGebruikt;
