import React from "react";
import PropTypes from "prop-types";
import "../assets/css/background.scss";

function Fout({ setToken }) {
  const handleSubmitRestart = (e) => {
    e.preventDefault();
    setToken("return");
  };
  return (
    <div className="box">
      <div className="my-5 pt-5">
        <div className="mx-5 px-5">
          <div>
            <p className="my-4 tekst">
              U heeft oftewel de verkeerde gegevens ingegeven,
              <br /> of u heeft geen toegang tot dit onderdeel van de site.
              <br /> Als gebruiker bent u niet geauthorizeerd <br />
              om de pagina aan te passen. Enkel admins hebben toegant <br /> tot
              dit gebied.
            </p>
          </div>
          <div>
            <form onSubmit={handleSubmitRestart}>
              <button type="submit" className="btn btn-primary">
                Opnieuw proberen
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

Fout.propTypes = { setToken: PropTypes.func.isRequired };

export default Fout;
