import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from "react";
import * as klassementAPI from "../api/klassement";

export const KlassementContext = createContext();
export const useKlassement = () => useContext(KlassementContext);

export const KlassementProvider = ({ children }) => {
  const [initialLoad, setInitialLoad] = useState(false);
  const [klassementa, setKlassementa] = useState([]);
  const [klassementb, setKlassementb] = useState([]);

  const refreshKlassementA = useCallback(async () => {
    try {
      const { data } = await klassementAPI.refreshKlassementA();
      setKlassementa(data.data);
      return data.data;
    } catch (error) {
      console.log(error);
    }
  }, []);

  const refreshKlassementB = useCallback(async () => {
    try {
      const { data } = await klassementAPI.refreshKlassementB();
      setKlassementb(data.data);
      return data.data;
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => {
    if (!initialLoad) {
      refreshKlassementA();
      refreshKlassementB();
      setInitialLoad(true);
    }
  }, [initialLoad, refreshKlassementA, refreshKlassementB]);

  const createKlassement = useCallback(
    async (info) => {
      await klassementAPI.createKlassement(info);
      refreshKlassementA();
      refreshKlassementB();
    },
    [refreshKlassementA, refreshKlassementB]
  );

  const deleteKlassement = useCallback(
    async (deelnemer) => {
      await klassementAPI.deleteKlassement(deelnemer);
      refreshKlassementA();
      refreshKlassementB();
    },
    [refreshKlassementA, refreshKlassementB]
  );

  const updateScore = useCallback(
    async (speler, alias, scorea, scoreb) => {
      await klassementAPI.updateScore(speler, alias, scorea, scoreb);
      refreshKlassementA();
      refreshKlassementB();
    },
    [refreshKlassementA, refreshKlassementB]
  );

  const updateKlassement = useCallback(
    async (deelnemer, deelname) => {
      await klassementAPI.updateKlassement(deelnemer, deelname);
      refreshKlassementA();
      refreshKlassementB();
    },
    [refreshKlassementA, refreshKlassementB]
  );

  const value = useMemo(
    () => ({
      klassementa,
      klassementb,
      createKlassement,
      deleteKlassement,
      updateKlassement,
      updateScore,
    }),
    [
      klassementa,
      klassementb,
      createKlassement,
      deleteKlassement,
      updateKlassement,
      updateScore,
    ]
  );

  return (
    <KlassementContext.Provider value={value}>
      {children}
    </KlassementContext.Provider>
  );
};
