import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { axios } from "../api/index";
import "../assets/css/background.scss";

function Antwoorden({ setToken }) {
  const [deelnemersA, setDeelnemersA] = useState([]);
  const [deelnemersB, setDeelnemersB] = useState([]);
  const [deelnemers, setDeelnemers] = useState([]);

  useEffect(() => {
    const getDeelnemers = async () => {
      try {
        const response = await axios.get("/deelnemers");
        setDeelnemers(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    getDeelnemers();
  }, []);

  useEffect(() => {
    const getDeelnemersA = async () => {
      try {
        const response = await axios.get("/klassement/a");
        setDeelnemersA(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    getDeelnemersA();
  }, []);

  useEffect(() => {
    const getDeelnemersB = async () => {
      try {
        const response = await axios.get("/klassement/b");
        setDeelnemersB(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    getDeelnemersB();
  }, []);

  const handleReturn = () => {
    setToken("buttons");
  };

  const afwezigenA = deelnemersA
    .filter((d) => d.ingedienda === 0)
    .map((d) => d.alias);
  const afwezigenB = deelnemersB
    .filter((d) => d.ingediendb === 0)
    .map((d) => d.alias);

  const finalA = deelnemers.filter((deelnemer) =>
    afwezigenA.includes(deelnemer.alias)
  );
  const finalB = deelnemers.filter((deelnemer) =>
    afwezigenB.includes(deelnemer.alias)
  );

  return (
    <div className="box">
      <div className="mt-5 pt-5">
        <div className="mx-5 px-5">
          <p className="my-4">
            <strong>
              <em className="titels">
                Hieronder staan alle spelers die nog geen antwoorden hebben
                ingediend
              </em>
            </strong>
          </p>
          <div className="row">
            <div className="col-lg-6">
              <h4 className="tekst">A</h4>
              {finalA.map((speler) => (
                <p key={speler.alias} className="tekst">
                  {speler.naam} - <strong>{speler.email}</strong> -{" "}
                  {speler.alias}
                </p>
              ))}
            </div>
            <div className="col-lg-6 tekst">
              <h4>B</h4>
              {finalB.map((speler) => (
                <p key={speler.alias}>
                  {speler.naam} - <strong>{speler.email}</strong> -{" "}
                  {speler.alias}
                </p>
              ))}
            </div>
          </div>
          <button className="btn col-md-6" onClick={handleReturn}>
            Terugkeren
          </button>
        </div>
      </div>
    </div>
  );
}

Antwoorden.propTypes = { setToken: PropTypes.func.isRequired };

export default Antwoorden;
