import { axios } from ".";

export const refreshKlassementA = async () => {
  return await axios.get("/klassement/a");
};

export const refreshKlassementB = async () => {
  return await axios.get("/klassement/b");
};

export const createKlassement = async (info) => {
  if (info.deelname === "A")
    await axios({
      method: "post",
      url: "/klassement",
      data: { alias: info.alias, scorea: 0, ingedienda: 0 },
    });
  else if (info.deelname === "B")
    await axios({
      method: "post",
      url: "/klassement",
      data: { alias: info.alias, scoreb: 0, ingediendb: 0 },
    });
  else
    await axios({
      method: "post",
      url: "/klassement",
      data: {
        alias: info.alias,
        scorea: 0,
        scoreb: 0,
        ingedienda: 0,
        ingediendb: 0,
      },
    });
};

export const deleteKlassement = async (deelnemer) => {
  await axios({
    method: "delete",
    url: `/klassement/${deelnemer[0].alias}`,
  });
};

export const updateScore = async (speler, alias, scorea, scoreb) => {
  if (speler[0].scorea == null)
    await axios({
      method: "put",
      url: `/klassement/${alias}`,
      data: { alias, scorea: null, scoreb },
    });
  else if (speler[0].scoreb == null)
    await axios({
      method: "put",
      url: `/klassement/${alias}`,
      data: { alias, scorea, scoreb: null },
    });
  else {
    await axios({
      method: "put",
      url: `/klassement/${alias}`,
      data: { alias, scorea, scoreb },
    });
  }
};

export const updateKlassement = async (deelnemer, deelname) => {
  let scoreb = 0;
  let scorea = 0;
  let ingedienda = 0;
  let ingediendb = 0;
  if (deelname === "A") {
    scoreb = null;
    ingediendb = null;
    if (deelnemer.scorea === undefined) scorea = 0;
  }
  if (deelname === "B") {
    scorea = null;
    ingedienda = null;
    if (deelnemer.scoreb === undefined) scoreb = 0;
  }
  await axios({
    method: "put",
    url: `/klassement/${deelnemer.alias}`,
    data: {
      alias: deelnemer[0].alias,
      scorea,
      scoreb,
      ingedienda,
      ingediendb,
    },
  });
};
