import { axios } from ".";

export const refreshDeelnames = async () => {
  return await axios.get("/deelnames");
};

export const updateDeelname = async () => {
  const jaren = await axios.get("20.126.37.178:9000/api/deelnames");
  const jaar = jaren.data.filter((jaar) => jaar.jaar === 2021);
  await axios({
    method: "put",
    url: "/deelnames/2021",
    data: {
      jaar: 2021,
      aantal: jaar[0].aantal + 1,
    },
  });
};

export const deleteDeelnemer = async (deelnemer) => {
  const jaren = await axios.get("/deelnames");
  const jaar = jaren.data.filter((jaar) => jaar.jaar === 2021);
  await axios({
    method: "put",
    url: `/deelnames/2021`,
    data: {
      jaar: 2021,
      aantal: jaar[0].aantal - 1,
    },
  });
};
