import React, { useState, useContext } from "react";
import PropTypes from "prop-types";
import { axios } from "../api/index";
import { useForm, FormProvider } from "react-hook-form";
import { DeelnemersContext } from "../contexts/deelnemers";
import { KlassementContext } from "../contexts/klassement";

const bcrypt = require("bcryptjs");

function FormulierenRechts({ setToken }) {
  const [email, setEmail] = useState();
  const [alias, setAlias] = useState();
  const [naam, setNaam] = useState();
  const [wachtwoord, setWachtwoord] = useState();
  const [deelname, setDeelname] = useState();

  const methods = useForm();
  const { handleSubmit } = methods;

  const contextKlassement = useContext(KlassementContext);
  const contextDeelnemers = useContext(DeelnemersContext);

  async function uitschrijven(deelnemer) {
    try {
      await contextKlassement.deleteKlassement(deelnemer);
      await contextDeelnemers.deleteDeelnemer(deelnemer);
    } catch (error) {
      console.log(error);
    }
  }

  async function aanpassen(deelnemer, deelname) {
    try {
      await contextDeelnemers.updateDeelnameDeelnemer(deelnemer, deelname);
      await contextKlassement.updateKlassement(deelnemer, deelname);
    } catch (error) {
      console.log(error);
    }
  }

  const handleSubmit2 = async () => {
    try {
      const deelnemers = await axios.get("/deelnemers");
      const deelnemer = deelnemers.data.filter(
        (deelnemer) =>
          deelnemer.email === email &&
          deelnemer.alias === alias &&
          deelnemer.naam === naam &&
          bcrypt.compareSync(wachtwoord, deelnemer.wachtwoord)
      );
      if (deelnemer[0] === undefined) setToken("verwijderen");
      else {
        await uitschrijven(deelnemer);
        setToken("succesvol uitgeschreven");
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleSubmit3 = async () => {
    if (deelname === undefined) setToken("leeg");
    else {
      try {
        const deelnemers = await axios.get("/deelnemers");
        const deelnemer = deelnemers.data.filter(
          (deelnemer) =>
            deelnemer.email === email.trim() &&
            deelnemer.alias === alias.trim() &&
            bcrypt.compareSync(wachtwoord.trim(), deelnemer.wachtwoord)
        );
        if (deelnemer[0] === undefined) setToken("verwijderen");
        else {
          await aanpassen(deelnemer, deelname);
          setToken("succesvol uitgeschreven");
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  return (
    <FormProvider {...methods}>
      <form
        className="mt-5 pt-5 ml-3 mr-3 tekst"
        onSubmit={handleSubmit(handleSubmit2)}
      >
        <h2>
          <ins>
            <strong className="titels">Uitschrijven</strong>
          </ins>
        </h2>
        <br />
        <div className="form-row">
          <div className="form-group col-md-8">
            <label>E-mailadres</label>
            <input
              required
              type="email"
              className="form-control"
              id="emailu"
              placeholder="E-mailadres"
              onChange={(mail) => setEmail(mail.target.value)}
              data-cy="mailFormulierenRechts"
            />
          </div>
          <div className="form-group col-md-4">
            <label>Alias</label>
            <input
              required
              type="text"
              className="form-control"
              id="aliasu"
              placeholder="Alias of bijnaam"
              onChange={(ali) => setAlias(ali.target.value)}
              data-cy="aliasFormulierenRechts"
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label>Voor- en achternaam</label>
            <input
              required
              type="text"
              className="form-control"
              id="naamu"
              placeholder="Voor- en achternaam"
              onChange={(name) => setNaam(name.target.value)}
              data-cy="naamFormulierenRechts"
            />
          </div>
          <div className="form-group col-md-6">
            <label>Wachtwoord</label>
            <input
              required
              type="password"
              className="form-control"
              id="wachtwoordu"
              placeholder="wachtwoord"
              onChange={(ww) => setWachtwoord(ww.target.value)}
              data-cy="wachtwoordFormulierenRechts"
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-12 mt-4">
            <button
              type="submit"
              className="btn"
              data-cy="uitschrijvenFormulierenRechts"
            >
              Uitschrijven
            </button>
          </div>
        </div>
      </form>
      <form
        className="mt-2 ml-3 mr-3 mb-5 pb-5 tekst"
        onSubmit={handleSubmit(handleSubmit3)}
      >
        <h2>
          <ins>
            <strong className="titels">Andere route</strong>
          </ins>
        </h2>
        <br />
        <div className="form-row">
          <div className="form-group col-md-8">
            <label>E-mailadres</label>
            <input
              required
              type="email"
              className="form-control"
              id="emaila"
              placeholder="E-mailadres"
              onChange={(mail) => setEmail(mail.target.value)}
              data-cy="mailAanpassen"
            />
          </div>
          <div className="form-group col-md-4">
            <label>Alias</label>
            <input
              required
              type="text"
              className="form-control"
              id="aliasa"
              placeholder="Alias of bijnaam"
              onChange={(ali) => setAlias(ali.target.value)}
              data-cy="aliasAanpassen"
            />
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label>Wachtwoord</label>
            <input
              required
              type="password"
              className="form-control"
              id="wachtwoorda"
              placeholder="wachtwoord"
              onChange={(ww) => setWachtwoord(ww.target.value)}
              data-cy="wachtwoordAanpassen"
            />
          </div>
          <div className="form-group col-md-6">
            <label>Welke route wenst u te volgen?</label>
            <select
              id="deelnamea"
              className="form-control"
              onChange={(d) => setDeelname(d.target.value)}
              data-cy="deelnameAanpassen"
            >
              <option value="">Kies...</option>
              <option value="A">A</option>
              <option value="B">B</option>
              <option value="AB">A en B</option>
            </select>
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-12 mt-4">
            <button type="submit" className="btn" data-cy="aanpassen">
              Route aanpassen
            </button>
          </div>
        </div>
      </form>
    </FormProvider>
  );
}

FormulierenRechts.propTypes = { setToken: PropTypes.func.isRequired };

export default FormulierenRechts;
