import { axios } from ".";

export const refreshTekstbestanden = async () => {
  return await axios.get("/tekstbestanden");
};

export const updateTekstbestand = async (home1, home2) => {
  await axios({
    method: "put",
    url: "/tekstbestanden/home1",
    data: { plaats: "home1", tekst: home1 },
  });
  await axios({
    method: "put",
    url: "/tekstbestanden/home2",
    data: { plaats: "home2", tekst: home2 },
  });
};
