import React, { useState, useContext } from "react";
import PropTypes from "prop-types";
import { axios } from "../api/index";
import { useForm, FormProvider } from "react-hook-form";
import { DeelnemersContext } from "../contexts/deelnemers";
import { DeelnamesContext } from "../contexts/deelnames";
import { KlassementContext } from "../contexts/klassement";
import "../assets/css/background.scss";

const bcrypt = require("bcryptjs");

function FormulierenLinks({ setToken }) {
  const [email, setEmail] = useState();
  const [alias, setAlias] = useState();
  const [naam, setNaam] = useState();
  const [stad, setStad] = useState();
  const [provincie, setProvincie] = useState();
  const [vorigeEditie, setVorigeEditie] = useState();
  const [password, setPassword] = useState();
  const [wachtwoordBevestigen, setWachtwoordBevestigen] = useState();
  const [deelname, setDeelname] = useState();

  const contextDeelnemers = useContext(DeelnemersContext);
  const contextDeelnames = useContext(DeelnamesContext);
  const contextKlassement = useContext(KlassementContext);

  const methods = useForm();
  const { handleSubmit } = methods;

  async function inschrijven(info) {
    try {
      await contextDeelnemers.createDeelnemer(info);
      await contextDeelnames.updateDeelname(info);
      return await contextKlassement.createKlassement(info);
    } catch (error) {
      console.log(error);
    }
  }

  const onSubmit = async () => {
    if (
      stad === undefined ||
      provincie === undefined ||
      vorigeEditie === undefined ||
      deelname === undefined
    )
      setToken("lege invoer");
    else if (password !== wachtwoordBevestigen) setToken("fout wachtwoord");
    else if (alias.trim().indexOf(" ") !== -1) {
      setToken("spaties");
    } else {
      const deelnemers = await axios.get("/deelnemers");
      let appel = false;
      deelnemers.data.forEach((deelnemer) => {
        if (deelnemer.naam === naam || deelnemer.alias === alias) {
          appel = true;
        }
      });
      if (appel) setToken("al gebruikt");
      else {
        await inschrijven({
          email,
          alias,
          naam,
          stad,
          provincie,
          vorigeEditie,
          wachtwoord: bcrypt.hashSync(password, bcrypt.genSaltSync()),
          deelname,
          admins: "USER",
        });
        setToken("succesvol");
      }
    }
  };

  return (
    <FormProvider {...methods} className="box">
      <form
        className="mt-5 pt-5 ml-3 mr-3 mb-5 pb-5 tekst back"
        onSubmit={handleSubmit(onSubmit)}
      >
        <h2>
          <ins>
            <strong className="titels">Inschrijven</strong>
          </ins>
        </h2>
        <br />
        <div className="form-row">
          <div className="form-group col-md-6">
            <label>E-mailadres</label>
            <input
              type="email"
              className="form-control"
              id="email"
              placeholder="E-mailadres"
              required
              onChange={(mail) => setEmail(mail.target.value)}
              data-cy="mailFormulierenLinks"
            />
          </div>
          <div className="form-group col-md-6">
            <label>Alias</label>
            <input
              type="text"
              className="form-control"
              id="alias"
              required
              placeholder="Alias of bijnaam"
              onChange={(ali) => setAlias(ali.target.value)}
              data-cy="aliasFormulierenLinks"
            />
          </div>
        </div>
        <div className="form-group">
          <label>Voor- en achternaam</label>
          <input
            type="text"
            className="form-control"
            id="naam"
            required
            placeholder="Voor- en achternaam"
            onChange={(name) => setNaam(name.target.value)}
            data-cy="naamFormulierenLinks"
          />
        </div>
        <div className="form-row">
          <div className="form-group col-md-4">
            <label>Stad</label>
            <select
              id="stad"
              className="form-control"
              onChange={(std) => setStad(std.target.value)}
              data-cy="stadFormulierenLinks"
            >
              <option value="">Kies...</option>
              <option value="Koksijde">Koksijde</option>
              <option value="Andere">Andere</option>
            </select>
          </div>
          <div className="form-group col-md-4">
            <label>Provincie</label>
            <select
              id="provincie"
              className="form-control"
              onChange={(pro) => setProvincie(pro.target.value)}
              data-cy="provincieFormulierenLinks"
            >
              <option value="">Kies...</option>
              <option value="West-Vlaanderen">West-Vlaanderen</option>
              <option value="Oost-Vlaanderen">Oost-Vlaanderen</option>
              <option value="Antwerpen">Antwerpen</option>
              <option value="Vlaams-Brabant">Vlaams-Brabant</option>
              <option value="Limburg">Limburg</option>
              <option value="Luxemburg">Luxemburg</option>
              <option value="Andere">Andere</option>
            </select>
          </div>
          <div className="form-group col-md-4">
            <label>Eerdere deelname</label>
            <select
              id="vorigeEditie"
              className="form-control"
              onChange={(ve) => setVorigeEditie(ve.target.value)}
              data-cy="vorigeEditieFormulierenLinks"
            >
              <option value="">Kies...</option>
              <option value="ja">Ja</option>
              <option value="nee">Nee</option>
            </select>
          </div>
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label>Wachtwoord</label>
            <input
              type="password"
              required
              className="form-control"
              id="wachtwoord"
              placeholder="Wachtwoord"
              onChange={(ww) => setPassword(ww.target.value)}
              data-cy="wachtwoordFormulierenLinks"
            />
          </div>
          <div className="form-group col-md-6">
            <label>Wachtwoord bevestigen</label>
            <input
              type="password"
              required
              className="form-control"
              id="wachtwoordBevestigen"
              placeholder="Wachtwoord bevestigen"
              onChange={(wwb) => setWachtwoordBevestigen(wwb.target.value)}
              data-cy="wachtwoordBevestigenFormulierenLinks"
            />
          </div>
        </div>
        <div className="form-row mb-5 pb-5">
          <div className="form-group col-md-8">
            <label>Welke route wenst u te volgen?</label>
            <select
              id="deelname"
              className="form-control"
              onChange={(d) => setDeelname(d.target.value)}
              data-cy="deelnameFormulierenLinks"
            >
              <option value="">Kies...</option>
              <option value="A">A</option>
              <option value="B">B</option>
              <option value="AB">A en B</option>
            </select>
          </div>
          <div className="form-group col-md-4 mt-4 pt-2">
            <button
              type="submit"
              className="btn"
              data-cy="inschrijvenFormulierenLinks"
            >
              Inschrijven
            </button>
          </div>
        </div>
      </form>
    </FormProvider>
  );
}

FormulierenLinks.propTypes = { setToken: PropTypes.func.isRequired };

export default FormulierenLinks;
