import React, { useState } from "react";
import FormulierenLinks from "../components/FormulierenLinks";
import FormulierenRechts from "../components/FormulierenRechts";
import LegeInvoer from "../subpages/LegeInvoer";
import FoutWachtwoord from "../subpages/FoutWachtwoord";
import AlGebruikt from "../subpages/AlGebruikt";
import Verwijderen from "../subpages/Verwijderen";
import Succesvol from "../subpages/Succesvol";
import SuccesvolUitgeschreven from "../subpages/SuccesvolUigeschreven";
import Spaties from "../subpages/Spaties";
import LegeDeelname from "../subpages/LegeDeelname";
import "../assets/css/background.scss";

function FormulierenRouter() {
  const [token, setToken] = useState();

  if (token === "lege invoer") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6">
            <FormulierenLinks setToken={setToken} />
          </div>
          <div className="col-6 mt-5 pt-5">
            <LegeInvoer />
          </div>
        </div>
      </div>
    );
  } else if (token === "fout wachtwoord") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6">
            <FormulierenLinks setToken={setToken} />
          </div>
          <div className="col-6 mt-5 pt-5">
            <FoutWachtwoord />
          </div>
        </div>
      </div>
    );
  } else if (token === "al gebruikt") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6">
            <FormulierenLinks setToken={setToken} />
          </div>
          <div className="col-6 mt-5 pt-5">
            <AlGebruikt />
          </div>
        </div>
      </div>
    );
  } else if (token === "verwijderen") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6 mt-5 pt-5">
            <Verwijderen />
          </div>
          <div className="col-6">
            <FormulierenRechts setToken={setToken} />
          </div>
        </div>
      </div>
    );
  } else if (token === "succesvol") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6">
            <FormulierenLinks setToken={setToken} />
          </div>
          <div className="col-6 mt-5 pt-5">
            <Succesvol />
          </div>
        </div>
      </div>
    );
  } else if (token === "succesvol uitgeschreven") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6 mt-5 pt-5">
            <SuccesvolUitgeschreven />
          </div>
          <div className="col-6">
            <FormulierenRechts setToken={setToken} />
          </div>
        </div>
      </div>
    );
  } else if (token === "spaties") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6">
            <FormulierenLinks setToken={setToken} />
          </div>
          <div className="col-6 mt-5 pt-5">
            <Spaties />
          </div>
        </div>
      </div>
    );
  } else if (token === "leeg") {
    return (
      <div className="back">
        <div className="row" style={{ width: window.innerWidth - 30 }}>
          <div className="col-6 mt-5 pt-5">
            <LegeDeelname />
          </div>
          <div className="col-6">
            <FormulierenRechts setToken={setToken} />
          </div>
        </div>
      </div>
    );
  } else
    return (
      <>
        <div className="back">
          <div className="row" style={{ width: window.innerWidth - 30 }}>
            <div className="col-6">
              <FormulierenLinks setToken={setToken} />
            </div>
            <div className="col-6">
              <FormulierenRechts setToken={setToken} />
            </div>
          </div>
        </div>
      </>
    );
}

export default FormulierenRouter;
