import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from "react";
import * as tekstbestandenAPI from "../api/tekstbestanden";

export const TekstbestandenContext = createContext();
export const useTekstbestanden = () => useContext(TekstbestandenContext);

export const TekstbestandenProvider = ({ children }) => {
  const [initialLoad, setInitialLoad] = useState(false);
  const [tekstbestanden, setTekstbestanden] = useState([]);

  const refreshTekstbestanden = useCallback(async () => {
    try {
      const { data } = await tekstbestandenAPI.refreshTekstbestanden();
      setTekstbestanden(data.data);
      return data.data;
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => {
    if (!initialLoad) {
      refreshTekstbestanden();
      setInitialLoad(true);
    }
  }, [initialLoad, refreshTekstbestanden, tekstbestanden]);

  const updateTekstbestand = useCallback(
    async (home1, home2) => {
      await tekstbestandenAPI.updateTekstbestand(home1, home2);
      refreshTekstbestanden();
    },
    [refreshTekstbestanden]
  );

  const value = useMemo(
    () => ({
      tekstbestanden,
      updateTekstbestand,
    }),
    [tekstbestanden, updateTekstbestand]
  );

  return (
    <TekstbestandenContext.Provider value={value}>
      {children}
    </TekstbestandenContext.Provider>
  );
};
