import React, { useState } from "react";
import Fout from "../subpages/Fout";
import Ingelogd from "../subpages/Ingelogd";
import Buttons from "../subpages/Buttons";
import Home from "../subpages/Home";
import Betaalt from "../subpages/Betaalt";
import Aliassen from "../subpages/Aliassen";
import Antwoorden from "../subpages/Antwoorden";
import Uitschrijven from "../subpages/Uitschrijven";
import Foutje from "../subpages/Foutje";
import "../assets/css/background.scss";

function Admin() {
  const [token, setToken] = useState();

  if (token === "home") {
    return (
      <>
        <Home setToken={setToken} />
      </>
    );
  } else if (token === "buttons") {
    return (
      <>
        <Buttons setToken={setToken} />
      </>
    );
  } else if (token === "scores") {
    return (
      <div className="mt-5 pt-3">
        <Ingelogd setToken={setToken} />
      </div>
    );
  } else if (token === "betaling") {
    return (
      <>
        <Betaalt setToken={setToken} />
      </>
    );
  } else if (token === "alias") {
    return (
      <>
        <Aliassen setToken={setToken} />
      </>
    );
  } else if (token === "ontvangen") {
    return (
      <>
        <Antwoorden setToken={setToken} />
      </>
    );
  } else if (token === "foutverwijderen") {
    return (
      <>
        <Betaalt setToken={setToken} />
        <Uitschrijven setToken={setToken} />
        <Foutje />
      </>
    );
  } else if (token === "verwijderen") {
    return (
      <>
        <div>
          <Betaalt setToken={setToken} />
        </div>
        <div>
          <Uitschrijven setToken={setToken} />
        </div>
      </>
    );
  } else if (token === "fout") {
    return (
      <>
        <Fout setToken={setToken} />
      </>
    );
  } else {
    return (
      <>
        <Buttons setToken={setToken} />
      </>
    );
  }
}

export default Admin;
