import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { axios } from "../api/index";
import { useMediaQuery } from "react-responsive";
import "../assets/css/background.scss";

function Aliassen({ setToken }) {
  const [deelnemers, setDeelnemers] = useState([]);

  const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isLaptop = useMediaQuery({ query: "(min-width: 1224px)" });

  useEffect(() => {
    const getDeelnemers = async () => {
      try {
        const response = await axios.get("/deelnemers");
        setDeelnemers(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    getDeelnemers();
  }, []);

  const handleReturn = () => {
    setToken("buttons");
  };

  return (
    <div className="box">
      {isLaptop && (
        <div className="mt-5 pt-5">
          <div className="mx-5 px-5">
            <p className="my-4">
              <strong>
                <em className="titels">
                  Hieronder staan alle aliassen van alle spelers
                </em>
              </strong>
            </p>
            {deelnemers.map((speler) => (
              <div key={speler.alias}>
                <div className="row tekst">
                  <div className="col-4" data-cy="namen">
                    {speler.naam}
                  </div>
                  <div className="col-4" data-cy="aliassen">
                    {speler.alias}
                  </div>
                  <div className="col-4" data-cy="mailadressen">
                    {speler.email}
                  </div>
                </div>
              </div>
            ))}
            <button className="btn col-md-6" onClick={handleReturn}>
              Terugkeren
            </button>
          </div>
        </div>
      )}
      {isMobile && (
        <div className="mt-5 pt-5 tekst">
          <div className=" mx-5 px-5">
            <p className="my-4">
              <strong>
                <em className="titels">
                  Hieronder staan alle aliassen van alle spelers
                </em>
              </strong>
            </p>
            {deelnemers.map((speler) => (
              <div key={speler.alias}>
                <div className="row">
                  <div className="col-12">{speler.naam}</div>
                  <div className="col-12">{speler.alias}</div>
                  <p className="col-12">{speler.email}</p>
                </div>
              </div>
            ))}
            <button className="btn col-md-6" onClick={handleReturn}>
              Terugkeren
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

Aliassen.propTypes = { setToken: PropTypes.func.isRequired };

export default Aliassen;
