import React, { useState, useContext } from "react";
import { axios } from "../api/index";
import { KlassementContext } from "../contexts/klassement";
import { useMediaQuery } from "react-responsive";
import "../assets/css/background.scss";

function Ingelogd({ setToken }) {
  const [alias, setAlias] = useState();
  const [scorea, setScorea] = useState();
  const [scoreb, setScoreb] = useState();

  const contextKlassement = useContext(KlassementContext);

  const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isLaptop = useMediaQuery({ query: "(min-width: 1224px)" });

  const clearFields = () => {
    document.getElementById("aliasu").value = "";
    document.getElementById("scorea").value = "";
    document.getElementById("scoreb").value = "";
  };

  const handleReturn = () => {
    setToken("buttons");
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.get(`/klassement/${alias}`);
      const speler = response.data;
      await contextKlassement.updateScore(speler, alias, scorea, scoreb);
      clearFields();
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="box mt-5 pt-3 tekst">
      {" "}
      {isLaptop && (
        <div className="mt-4 pt-2">
          <form className=" mx-5 px-5">
            <p className="my-4">
              <strong>
                <em className="titels">
                  Hier kan u de scores van de spelers aanpassen
                </em>
              </strong>
            </p>
            <div className="row">
              <div className="col-4">
                <input
                  required
                  type="text"
                  className="form-control mt-5"
                  id="aliasu"
                  placeholder="Alias of bijnaam"
                  onChange={(ali) => setAlias(ali.target.value)}
                />
              </div>
              <div className="col-4">
                <input
                  required
                  type="number"
                  className="form-control mt-5"
                  id="scorea"
                  placeholder="Score A"
                  onChange={(sa) => setScorea(sa.target.value)}
                />
              </div>
              <div className="col-4">
                <input
                  required
                  type="number"
                  className="form-control mt-5"
                  id="scoreb"
                  placeholder="Score b"
                  onChange={(sb) => setScoreb(sb.target.value)}
                />
              </div>
            </div>
            <div className="form-group col-md-12 mb-5">
              <button
                type="submit"
                className="btn col-md-4 mr-3"
                onClick={handleSubmit}
              >
                Updaten
              </button>
              <button
                type="submit"
                className="btn col-md-4 ml-3"
                onClick={handleReturn}
              >
                Terugkeren
              </button>
            </div>
          </form>
        </div>
      )}
      {isMobile && (
        <div className="mt-4 pt-2">
          <form className="border border-danger rounded mx-5 px-5">
            <p className="my-4">
              <strong>
                <em>Hier kan u de scores van de spelers aanpassen</em>
              </strong>
            </p>
            <div className="row">
              <div className="col-12">
                <input
                  required
                  type="text"
                  className="form-control mt-5"
                  id="aliasu"
                  placeholder="Alias of bijnaam"
                  onChange={(ali) => setAlias(ali.target.value)}
                />
              </div>
              <div className="col-12">
                <input
                  required
                  type="number"
                  className="form-control mt-5"
                  id="scorea"
                  placeholder="Score A"
                  onChange={(sa) => setScorea(sa.target.value)}
                />
              </div>
              <div className="col-12">
                <input
                  required
                  type="number"
                  className="form-control mt-5"
                  id="scoreb"
                  placeholder="Score b"
                  onChange={(sb) => setScoreb(sb.target.value)}
                />
              </div>
            </div>
            <div className="form-group col-md-12 mb-5">
              <button
                type="submit"
                className="btn btn-primary col-md-4 mr-3"
                onClick={handleSubmit}
              >
                Updaten
              </button>
              <button
                type="submit"
                className="btn btn-primary col-md-4 ml-3"
                onClick={handleReturn}
              >
                Terugkeren
              </button>
            </div>
          </form>
        </div>
      )}
    </div>
  );
}

export default Ingelogd;
