/* eslint-disable react/jsx-no-target-blank */
import React from "react";
import { Link, withRouter } from "react-router-dom";
import { useMediaQuery } from "react-responsive";

function Navigation(props) {
  const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isLaptop = useMediaQuery({ query: "(min-width: 1224px)" });

  return (
    <>
      {isLaptop && (
        <div className="navigation">
          <header className="fixed-top">
            <nav className="navbar navbar-expand navbar-dark outro">
              <div className="container">
                <Link className="tekst" to="/">
                  <span>Woarist? Doarist</span>!
                </Link>
                <div>
                  <ul className="navbar-nav ml-auto">
                    <li
                      className={`nav-item  ${
                        props.location.pathname === "/" ? "active" : ""
                      }`}
                    >
                      <Link className="nav-link" to="/">
                        Home
                        <span className="sr-only"></span>
                      </Link>
                    </li>

                    <li
                      className={`nav-item  ${
                        props.location.pathname === "/antwoorden"
                          ? "active"
                          : ""
                      }`}
                    >
                      <Link className="nav-link" to="/antwoorden">
                        Antwoorden
                      </Link>
                    </li>
                    <li
                      className={`nav-item  ${
                        props.location.pathname === "/klassement"
                          ? "active"
                          : ""
                      }`}
                    >
                      <Link className="nav-link" to="/klassement">
                        Klassement
                      </Link>
                    </li>
                    <li
                      className={`nav-item ${
                        props.location.pathname === "/formulieren"
                          ? "active"
                          : ""
                      }`}
                    >
                      <Link className="nav-link" to="/formulieren">
                        In- of Uitschrijven
                      </Link>
                    </li>
                    <li
                      className={`nav-item ${
                        props.location.pathname === "/contact" ? "active" : ""
                      }`}
                    >
                      <Link className="nav-link" to="/contact">
                        Contact
                      </Link>
                    </li>
                    <li
                      className={`nav-item ${
                        props.location.pathname === "/login" ? "active" : ""
                      }`}
                    >
                      <Link className="nav-link" to="/login">
                        Login
                      </Link>
                    </li>
                  </ul>
                </div>
              </div>
            </nav>
          </header>
        </div>
      )}
      {isMobile && (
        <div className="navigation">
          <header className="">
            <nav className="outro">
              <div className="container">
                <div>
                  <ul className="navbar-nav my-0 py-0">
                    <li
                      className={`nav-item  ${
                        props.location.pathname === "/" ? "active" : ""
                      }`}
                    >
                      <Link className="nav-link" to="/">
                        Home
                        <span className="sr-only"></span>
                      </Link>
                    </li>

                    <li
                      className={`nav-item  ${
                        props.location.pathname === "/antwoorden"
                          ? "active"
                          : ""
                      }`}
                    >
                      <Link className="nav-link" to="/antwoorden">
                        Antwoorden
                      </Link>
                    </li>
                    <li
                      className={`nav-item  ${
                        props.location.pathname === "/klassement"
                          ? "active"
                          : ""
                      }`}
                    >
                      <Link className="nav-link" to="/klassement">
                        Klassement
                      </Link>
                    </li>
                    <li
                      className={`nav-item ${
                        props.location.pathname === "/formulieren"
                          ? "active"
                          : ""
                      }`}
                    >
                      <Link className="nav-link" to="/formulieren">
                        In- of Uitschrijven
                      </Link>
                    </li>
                    <li
                      className={`nav-item ${
                        props.location.pathname === "/contact" ? "active" : ""
                      }`}
                    >
                      <Link className="nav-link" to="/contact">
                        Contact
                      </Link>
                    </li>
                    <li
                      className={`nav-item ${
                        props.location.pathname === "/admin" ? "active" : ""
                      }`}
                    >
                      <Link className="nav-link" to="/admin">
                        Admin
                      </Link>
                    </li>
                  </ul>
                </div>
              </div>
            </nav>
          </header>
        </div>
      )}
    </>
  );
}

export default withRouter(Navigation);
