import React from "react";
import { Bar } from "react-chartjs-2";

export default function GroupedBar(props) {
  const {
    provincieVlaamsBrabant,
    provincieAntwerpen,
    provincieLimburg,
    provincieLuxemburg,
    provincieOostVlaanderen,
    provincieWestVlaanderen,
    provincieKoksijde,
    provincieAndere,
    zktAenB,
    zktA,
    zktB,
    deelname2015,
    deelname2016,
    deelname2017,
    deelname2018,
    deelname2019,
    deelname2020,
    deelname2021,
    oudgedienden,
    nieuwe,
  } = props;
  const dataProvincie = {
    labels: ["Deelnemers per zoektocht"],
    datasets: [
      {
        label: "Vlaams Brabant",
        data: [provincieVlaamsBrabant],
        backgroundColor: "#AA0000",
      },
      {
        label: "Antwerpen",
        data: [provincieAntwerpen],
        backgroundColor: "#EF0107",
      },
      {
        label: "Limburg",
        data: [provincieLimburg],
        backgroundColor: "#D2122E",
      },
      {
        label: "Luxemburg",
        data: [provincieLuxemburg],
        backgroundColor: "#FF033E",
      },
      {
        label: "Oost-Vlaanderen",
        data: [provincieOostVlaanderen],
        backgroundColor: "#EE204D",
      },
      {
        label: "West-Vlaanderen",
        data: [provincieWestVlaanderen],
        backgroundColor: "#E52B50",
      },
      {
        label: "Koksijde",
        data: [provincieKoksijde],
        backgroundColor: "#DE3163",
      },
      {
        label: "Andere",
        data: [provincieAndere],
        backgroundColor: "#fd5c63",
      },
    ],
  };

  const dataZkt = {
    labels: ["Deelnemers per zoektocht"],
    datasets: [
      {
        label: "Aantal zkt A + B",
        data: [zktAenB],
        backgroundColor: "#AA0000",
      },
      {
        label: "Aantal zkt A alleen",
        data: [zktA],
        backgroundColor: "#EF0107",
      },
      {
        label: "Aantal zkt B alleen",
        data: [zktB],
        backgroundColor: "#D2122E",
      },
      {
        label: "Totaal zkt A",
        data: [zktA + zktAenB],
        backgroundColor: "#FF033E",
      },
      {
        label: "Totaal zkt B",
        data: [zktB + zktAenB],
        backgroundColor: "#EE204D",
      },
    ],
  };

  const dataJaar = {
    labels: ["Aantal deelnemers / jaar"],
    datasets: [
      {
        label: "2015",
        data: [deelname2015],
        backgroundColor: "#AA0000",
      },
      {
        label: "2016",
        data: [deelname2016],
        backgroundColor: "#EF0107",
      },
      {
        label: "2017",
        data: [deelname2017],
        backgroundColor: "#D2122E",
      },
      {
        label: "2018",
        data: [deelname2018],
        backgroundColor: "#FF033E",
      },
      {
        label: "2019",
        data: [deelname2019],
        backgroundColor: "#EE204D",
      },
      {
        label: "2020",
        data: [deelname2020],
        backgroundColor: "#E52B50",
      },
      {
        label: "2021",
        data: [deelname2021],
        backgroundColor: "#DE3163",
      },
      {
        label: "Oudgedienden",
        data: [oudgedienden],
        backgroundColor: "#fd5c63",
      },
      {
        label: "Nieuwe",
        data: [nieuwe],
        backgroundColor: "#F88379",
      },
    ],
  };

  const options = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };

  return (
    <>
      <div className="container tekst">
        <h2 className="title">Deelnemers per provincie</h2>
        <Bar data={dataProvincie} options={options} />
        <h2 className="title pt-5">Deelnemers per zoektocht</h2>
        <Bar data={dataZkt} options={options} />
        <h2 className="title pt-5">Aantal deelnemers / jaar</h2>
        <Bar data={dataJaar} options={options} />
      </div>
    </>
  );
}
