import {
  createContext,
  useState,
  useEffect,
  useCallback,
  useContext,
  useMemo,
} from "react";
import * as deelnemersAPI from "../api/deelnemers";

export const DeelnemersContext = createContext();
export const useDeelnemers = () => useContext(DeelnemersContext);

export const DeelnemersProvider = ({ children }) => {
  const [initialLoad, setInitialLoad] = useState(false);
  const [deelnemers, setDeelnemers] = useState([]);

  const refreshDeelnemers = useCallback(async () => {
    try {
      const { data } = await deelnemersAPI.refreshDeelnemers();
      setDeelnemers(data.data);
      return data.data;
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => {
    if (!initialLoad) {
      refreshDeelnemers();
      setInitialLoad(true);
    }
  }, [initialLoad, refreshDeelnemers, deelnemers]);

  const createDeelnemer = useCallback(
    async (info) => {
      try {
        await deelnemersAPI.createDeelnemer(info);
        refreshDeelnemers();
      } catch (error) {
        console.log(error);
      }
    },
    [refreshDeelnemers]
  );

  const updateDeelnameDeelnemer = useCallback(
    async (deelnemer, deelname) => {
      await deelnemersAPI.updateDeelnameDeelnemer(deelnemer, deelname);
      refreshDeelnemers();
    },
    [refreshDeelnemers]
  );

  const deleteDeelnemer = useCallback(
    async (deelnemer) => {
      await deelnemersAPI.deleteDeelnemer(deelnemer);
      refreshDeelnemers();
    },
    [refreshDeelnemers]
  );

  const value = useMemo(
    () => ({
      deelnemers,
      createDeelnemer,
      updateDeelnameDeelnemer,
      deleteDeelnemer,
    }),
    [deelnemers, createDeelnemer, updateDeelnameDeelnemer, deleteDeelnemer]
  );

  return (
    <DeelnemersContext.Provider value={value}>
      {children}
    </DeelnemersContext.Provider>
  );
};
