import React, { useEffect, useState } from "react";
import { axios } from "../api/index";
import { useMediaQuery } from "react-responsive";
import "../assets/css/background.scss";

function Klassement() {
  const [deelnemersA, setDeelnemersA] = useState([]);
  const [deelnemersB, setDeelnemersB] = useState([]);

  const isMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isLaptop = useMediaQuery({ query: "(min-width: 1224px)" });

  useEffect(() => {
    const getDeelnemersA = async () => {
      try {
        const response = await axios.get(
          "http://localhost:9000/api/klassement/a"
        );
        setDeelnemersA(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    getDeelnemersA();
  }, []);

  useEffect(() => {
    const getDeelnemersB = async () => {
      try {
        const response = await axios.get(
          "http://localhost:9000/api/klassement/b"
        );
        setDeelnemersB(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    getDeelnemersB();
  }, []);

  return (
    <>
      {isLaptop && (
        <>
          <div className="box">
            <div
              className="row pt-5 mt-5"
              style={{ width: window.innerWidth - 30 }}
            >
              <div className="col-6">
                <h2>
                  <strong>
                    <ins className="titels">Klassement A</ins>
                  </strong>
                </h2>

                {deelnemersA.map((speler) => (
                  <p className="tekst">
                    {speler.alias} met {speler.scorea} punten
                  </p>
                ))}
              </div>
              <div className="col-6">
                <h2>
                  <strong>
                    <ins className="titels">Klassement B</ins>
                  </strong>
                </h2>

                {deelnemersB.map((speler) => (
                  <p className="tekst">
                    {speler.alias} met {speler.scoreb} punten
                  </p>
                ))}
              </div>
            </div>
          </div>
        </>
      )}
      {isMobile && (
        <div className="mb-5 pb-5">
          <div className="mb-5 pb-5">
            <div className="col-12 tekst mb-5 pb-5">
              <h2>
                <strong>
                  <ins className="titels">Klassement A</ins>
                </strong>
              </h2>
              {deelnemersA.map((speler) => (
                <p data-cyL={speler.alias}>
                  {speler.alias} met {speler.scorea} punten
                </p>
              ))}
            </div>
            <div className="col-12 tekst mb-5 pb-5">
              <h2>
                <strong>
                  <ins className="titels">Klassement B</ins>
                </strong>
              </h2>
              {deelnemersB.map((speler) => (
                <p data-cyR={speler.alias}>
                  {speler.alias} met {speler.scoreb} punten
                </p>
              ))}
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default Klassement;
