import React from "react";
import PropTypes from "prop-types";
import "../assets/css/background.scss";

function Buttons({ setToken }) {
  const handleSubmitButtons = (token) => {
    setToken(token);
  };

  return (
    <div className="box tekst">
      <div className="my-5 pt-5">
        <div className="mx-5 px-5">
          <div className="mb-5">
            <p className="my-4">
              <em>
                <strong className="titels">
                  Hier kan u kiezen wat u exact wenst te doen.
                </strong>
              </em>
            </p>
            <div>
              <form>
                <select
                  id="buttons"
                  onChange={(b) => handleSubmitButtons(b.target.value)}
                  className="form-control"
                >
                  <option value="">Kies...</option>
                  <option value="home">Welkomstteksten aanpassen</option>
                  <option value="scores">Scores aanpassen</option>
                  <option value="betaling">Betalingen controleren</option>
                  <option value="alias">Aliassen opvragen</option>
                  <option value="ontvangen">
                    Wie heeft er al antwoorden doorgestuurd
                  </option>
                </select>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

Buttons.propTypes = { setToken: PropTypes.func.isRequired };

export default Buttons;
