import { useCallback } from "react";
import { FormProvider, useForm } from "react-hook-form";
import { useHistory } from "react-router";
import LabelInput from "../components/LabelInput";
import { useLogin, useSession } from "../contexts/auth";

const validationRules = {
  naam: {
    required: true,
  },
  password: {
    required: true,
  },
};

function Login() {
  const history = useHistory();
  const { loading, error } = useSession();
  const login = useLogin();
  const methods = useForm();
  const { handleSubmit, reset } = methods;

  const handleLogin = useCallback(
    async ({ naam, password }) => {
      const success = await login(naam, password);
      if (success) {
        // we can't come back to login
        history.replace("/admin");
      }
    },
    [history, login]
  );

  const handleCancel = useCallback(() => {
    reset();
  }, [reset]);

  //   if (isAuthed) {
  //     return <Redirect from="/login" to="/" />;
  //   }

  return (
    <FormProvider {...methods} my-5 py-5>
      <div className="mx-auto w-25 my-5 py-5">
        <h1 className="tekst">Sign in</h1>
        <form className="my-5 py-5" onSubmit={handleSubmit(handleLogin)}>
          {error ? <p className="titels">{error}</p> : null}
          <LabelInput
            label="naam"
            type="text"
            defaultValue=""
            data-cy="email_input"
            placeholder="alias"
            validation={validationRules.naam}
            className="text-secondary"
          />

          <LabelInput
            label="password"
            type="password"
            defaultValue=""
            data-cy="password_input"
            validation={validationRules.password}
          />

          <div className="flex flex-row justify-end">
            <button
              data-cy="submit_btn"
              type="submit"
              disabled={loading}
              className="disabled:opacity-50"
            >
              Sign in
            </button>

            <button type="button" onClick={handleCancel}>
              Cancel
            </button>
          </div>
        </form>
      </div>
    </FormProvider>
  );
}

export default Login;
