import React from "react";
import "../assets/css/contact.css";

function Contact() {
  return (
    <>
      <div className="contact container-fluid px-1 py-5 mx-auto mt-5">
        <div className="row d-flex justify-content-center">
          <div className="col-xl-7 col-lg-8 col-md-9 col-11 text-center">
            <div className="card mt-3 pt-5">
              <h5 className="text-center my-4 titels">Vragenformulier</h5>
              <form
                className="form-card"
                onsubmit="event.preventDefault()"
                action="mailto:xeo.gillis@gmail.com"
                method="post"
                enctype="text/plain"
              >
                <div className="row justify-content-between text-left">
                  <div className="form-group col-sm-6 flex-column d-flex">
                    <p className="info mt-4">
                      Hulp nodig bij dit formulier? Houd uw muis hier.
                    </p>
                    <p className="hide">
                      {" "}
                      Eenmaal u op "verzenden" klikt zal u doorverwezen worden
                      naar uw <br />
                      e-mailserver. Hier hoeft u niets te doen naast opnieuw op
                      "verzenden" <br />
                      drukken zodat de mail bij ons terecht komt. Zo zullen wij
                      u kunnen <br />
                      contacteren.
                    </p>
                  </div>
                </div>
                <div className="row justify-content-between text-left">
                  <div className="form-group col-12 flex-column d-flex">
                    {" "}
                    <input
                      type="textarea"
                      id="vraag"
                      name="vraag"
                      placeholder="Stel hier uw vraag"
                    />{" "}
                  </div>
                </div>
                <div className="row justify-content-end">
                  <div className="form-group col-sm-6">
                    {" "}
                    <button type="submit" className="btn btn-block">
                      Verzenden
                    </button>{" "}
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Contact;
